import React, { Component } from 'react';
import Jumbotron from 'react-bootstrap/lib/Jumbotron';

 class Jumbo extends Component {

   constructor(props) {
     super(props);
     this.state = {
       count: 90
     };
   }
   render() {
     return(
       <div>
         <Jumbotron>
          <p className="lead">{this.state.count}</p>
         </Jumbotron>
       </div>
     );
   }
 }

export default Jumbo;



